const messages = {
  after: (field, [target]) => ` ${field}${target}`,
  alpha_dash: (field) => ` ${field}`,
  alpha_num: (field) => `${field} .`,
  alpha_spaces: (field) => ` ${field} .`,
  alpha: (field) => ` ${field} .`,
  before: (field, [target]) => ` ${field} ${target} .`,
  between: (field, [min, max]) => ` ${field} .`,
  numberInRange: (field, [min, max]) => ` ${field} .`,
  confirmed: (field, [confirmedField]) => ` ${field} ${confirmedField}.`,
  date_between: (field, [min, max]) => ` ${field}${min}${max}.`,
  date_format: (field, [format]) => ` ${field}${format}.`,
  decimal: (field, [decimals] = ['*']) => ` ${field}${decimals === '*' ? '' : decimals}.`,
  digits: (field, [length]) => ` ${field}  ${length}`,
  dimensions: (field, [width, height]) => ` ${field} ${width}  ${height} .`,
  email: (field) => ` ${field} .`,
  ext: (field) => ` ${field} .`,
  image: (field) => ` ${field} .`,
  in: (field) => ` ${field} .`,
  ip: (field) => ` ${field} .`,
  max: (field, [length]) => ` ${field}${length}.`,
  mimes: (field) => ` ${field} .`,
  min: (field, [length]) => ` ${field} ${length} .`,
  not_in: (field) => ` ${field}.`,
  numeric: (field) => ` ${field} .`,
  regex: (field) => ` ${field}.`,
  required: (field) => `${field}.`,
  size: (field, [size]) => ` ${field}  ${size} KB.`,
  url: (field) => ` ${field}url.`,
  taskExist: (field) => ` ${field}`,
  alarmExist: (field) => ` ${field}`,
  array: (field) => ` ${field}`,
  usersDisabled: (field) => ` ${field}`,
  filterSuffix: (field) => ``,
  existGroupName: (field) => ``,
  isNumberRange: (field) => ``,
  isStartMoreThanEndValue: (field) => ``,
  isStartTimeMoreThanEndTime: () => ``,
  isNumber: () => ``,
  valueRules: (field) => `${field}`,
  isNumberInRange: (field) => ``,
  moreThanNumberEquals: () => ``,
  lessThanNumberEquals: () => ``,
  specialCharacterRule: (field) => `${field} '|' `,
  isKeyword: () => ``,
  isDate: (field) => `${field}`,
  sumValidate: (field) => `${field}100`,
  repeatValues: (field) => ``,
  nameTooLong: (field) => `${field}30`,
  veryLong: (field) => `${field}100`,
  nameCharacter: (field) => `${field}'_'`,
  nameInitial: (field) => `${field}`,
  nameExist: (field) => `${field}`,
  codeCharacter: (field) => `${field}'_'`,
  codeInitial: (field) => `${field}`,
  keywordConflict: (field) => ``
}

const locale = {
  name: 'zh_CN',
  messages,
  attributes: {}
}

export default locale



